@import "../../Global.scss";

.portfolio{
    background-color:rgb(231, 242, 248);
    display: flex;
    flex-direction: column;
    align-items: center;

    ul{
        margin: 10px;
        padding: 20px;
        padding-right: 50px;
        list-style: none;
        display: flex;
        justify-content: center; 
        font-weight: bold; 

        @include mobile{
            margin: 10px 0;
            flex-wrap: wrap;
            justify-content: center;
        }
}
    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile{
            width: 100%;
        }

        .item{
            width: 230px;
            height: 150px;
            border-radius: 20px;
            border: 1px solid rgb(241, 240, 240);
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;

            @include mobile{
                width: 130px;
                height: 100px;
            }

            h3{
                position: absolute;
                font-size: 20px;
            }

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover{
                background-color: $mainColor;

                img{
                    opacity: .2;
                    z-index: 0;
                }
            }
        }
    }
    
}