@import "../../Global.scss";

.intro {
    background-color: white;
    display: flex;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }

    .left{
        flex: 0.5;
        overflow: hidden;

        .imgContainer {
            width: 800px;
            height: 800px;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            @include mobile {
                align-items: flex-start;
            }

        img{
            height: 100%;

            @include mobile {
                height: 50%;
            }
        }
      }
    }
    .right{
        flex: 0.5;
        position: relative;

        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile{
                padding-left: 0;
                align-items: center;

            }

            h1{
                font-size: 60px;
                margin: 10px 0;

                @include mobile{
                    font-size: 50px;
                }
            }
            h2{ font-size: 35px;

                @include mobile{
                    font-size: 30px;
                }
            }
            h3{
                font-size: 30px ;
                display: flex;

                @include mobile{
                    font-size: 22px;
                }

                span{
                    font-size: inherit;
                    color: rgb(255, 168, 6);
                    padding-left: 3px;
                   
                }

                @keyframes blink {
                    50%{
                        opacity: 1;
                    }

                    100%{
                        opacity: 0;
                    }
                }
            }
        }
        a{
            position: absolute;
            bottom: 10px;
            left: 40%;

            img{
                width: 30px;
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink {
                100% {
                    opacity: 0;
                }
            }
        }
    }
}